          "gameId":90,
          "name":"Star Wars: Episode 1  Clash of the Lightsabers",
          "description":"Spewed out amidst the rest of the 'Phantom Menace' merchandise, this little card game from Hasbro actually rises to a decent level for the true Gamer.  The game focuses on the duel between Qui-Gon and Darth Maul.  Players face off in three battles per round trying to beat the other in two out of the three battles.  If you win a round then you score a point on the victory track.  Players have to manage a hand of cards and decide when to press forward and when to concede a battle.&#10;&#10;From the box:&#10;&#10;Relive the Final Duel in a one-on-one contest of strategy and skill!&#10;&#10;It's your turn to determine the fate of Naboo!  Pick who you will be -- Sith Lord Darth Maul or Jedi Master Qui-Gon Jinn -- and duel to the finish!&#10;&#10;Hand by hand, round by round, use your Battle Deck to attack and defend. Use strategy. Use skill. Use the Force. Move your figure through thte FInal Duel cards towards victory until the battle is won. Who will be the winner?&#10;&#10;Contents&#10;&#10;&#10;    55 Qui-Gon Jinn Battle Cards&#10;    55 Darth Maul Battle Cards&#10;    5 Qui-Gon Jinn Final Duel Cards&#10;    5 Darth Maul Final Duel Cards&#10;    1 Qui-Gon Jinn Pewter Figure&#10;    1 Darth Maul Pewter Figure&#10;&#10;&#10;",
